.theme-switcher {
    display: flex;
    align-items: center;
    cursor: pointer;

    @include vp--529 {
        background-color: #C9C9C9;
        padding: 10px 12px;
        padding-right: 24px;
        border-radius: 8px;
    }
}

.theme-switcher__label {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-shrink: 0;
    margin-right: 28%;
    width: 22px;
    height: 12px;
    border: $color-default-black solid 2px;
    border-radius: 100px;
    position: relative;
    transition: background-color .2s;
    background-color: transparent;
}

.theme-switcher__label .theme-switcher__btn {
    content: '';
    position: absolute;
    top: 50%;
    left: 1px;
    transform: translateY(-50%);
    width: 6px;
    height: 6px;
    border-radius: 45px;
    transition: 0.2s;
    background:  $color-default-black;
}

.theme-switcher__checkbox:checked + .theme-switcher__label .theme-switcher__btn {
    left: calc(100% - 8px);
}

.theme-switcher svg {
    width: 22px;
    height: 22px;
    flex-shrink: 0;
}
