.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;

  clip: rect(0 0 0 0);

  order: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.no-transition {
  transition: none !important;
}

.error-boundary {
  font-size: 12px;
  line-height: 1.2;
  font-weight: bold;
  color: darkred !important;
}
